<template>
  <div class="AssessBall">
    <img src="../images/component/AssessBall/AssessBall__outside.png" class="AssessBall__outside tranform" >
    <img src="../images/component/AssessBall/AssessBall__middle.png" class="AssessBall__middle" >
    <router-link to="/SelfAssessment/result">
      <p class="AssessBall__title">评估</p>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'AssessBall'

}
</script>

<style scoped>

.AssessBall__outside{
  width: 200px;
  height: 94px;
  position: absolute;
  left: 0;
}
.AssessBall__middle{
  width: 94px;
  height: 94px;
  position: absolute;
  left: 0;
}
.AssessBall__title{
  color: rgb(177, 217, 218);
  font-size: 16px;
  font-weight: bold;
  position: relative;
  left: 30px;
  top: 40px;

}
.tranform{
  animation: rotate 1s linear ;
  transform-origin: 47px 47px;
}

@keyframes rotate{
  0% {
    transform: rotate(0);

  }
  50% {
    transform: rotate(45deg);
  }
  100%{
    transform: rotate(0);
  }
}
</style>



