<template>
  <div class="StepBar">
    <div class="StepBar__normal" v-show="!active">
      <img src="../images/component/StepBar/StepBar.svg" width="120px" height="50xpx">
      <div class="StepBar__normal__title">
        <p class="StepBar__normal__title--CN">{{ titleCN }}</p>
        <p class="StepBar__normal__title--EN">{{ titleEN }}</p>
      </div>
    </div>
    <div class="StepBar__active" v-show="active" >
      <img src="../images/component/StepBar/StepBar--active.svg" width="160px" height="60xpx">
      <div class="StepBar__active__title">
        <p class="StepBar__active__title--CN">{{ titleCN }}</p>
        <p class="StepBar__active__title--EN">{{ titleEN }}</p>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'StepBar',
  props: {
    active: Boolean,
    titleCN: String,
    titleEN: String
  }
}
</script>

<style scoped>

.StepBar{
  position: relative;
  width: 150px;
  height: 50px;
}
.StepBar img{
  position: absolute;
  top: 0;
  left: 0;
}
.StepBar__normal{
  position: absolute;
  left: 20px;
  top: 5px;
  width: 120px;
  height: 50px;
  color: rgba(177, 218, 217,0.5);
}
.StepBar__normal__title{
  text-align: center;
  padding-top: 12px;
}
.StepBar__normal__title--CN{
  font-size: 12px;
}
.StepBar__normal__title--EN{
  font-size: 8px;
}



.StepBar__active{
  width: 160px;
  height: 60px;
  color: rgb(177, 218, 217);
}
.StepBar__active__title{
  text-align: center;
  padding-top: 15px;
}
.StepBar__active__title--CN{
  font-size: 18px;
}
.StepBar__active__title--EN{
  font-size: 12px;
}
</style>



