<template>
  <div class="school" @click='toggle'>
    <img src="../images/component/school/jieguo_shcool_bg_nomal.png" class="school__nomralBg" v-show="!select">
    <img src="../images/component/school/jieguo_shcool_bg_selected.png" class="school__selectedBg" v-show="select">
    <div class="school__logo">
      <slot name="logo" ></slot>
    </div>
    <div class="school__title">
      <p class="school__title__p">{{ titleCN }}</p>
      <p class="school__title__p">{{ titleEN }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'school',
  props: {
    titleCN: String,
    titleEN: String
  },
  data () {
    return {
      select: false
    }
  },
  methods: {
    toggle () {
      this.select = !this.select
    }
  }

}
</script>

<style scoped>
.school{
  cursor: pointer;
  width: 145px;
  height: 145px;
  position: relative;
}
.school__logo{
  position: absolute;
  top: 20px;
  left: 40px;
}
.school__logo img{
  width: 60px;
  height: 63px;
}

.school__title{
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 0;
  text-align: center;
}
.school__title__p{
  font-size: 12px;
  margin-top: 5px;
  color: rgba(177, 217, 218,0.8)
}

</style>



